<template>
  <div class="my-5 row justify-content-center">
    <div class="col-md-12 text-center" v-if="order.is_paid">
      <span class="icon-check_circle display-3 text-success"></span>
      <h2 class="display-3 text-black">感謝你!</h2>
      <p class="lead mb-5">你的訂單已完成</p>
    </div>
    <form class="col-md-6 pt-5" @submit.prevent="payOrder">
      <table class="table">
        <thead>
          <th>品名</th>
          <th>數量</th>
          <th>單價</th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="2" class="text-right">總計</td>
            <td class="text-right">{{ order.total }}</td>
          </tr>
        </tfoot>
      </table>

      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th>姓名</th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th>收件人電話</th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th>收件人地址</th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th>付款狀態</th>
            <td>
              <span v-if="!order.is_paid">尚未付款</span>
              <span v-else class="text-success">付款完成</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-danger">確認付款去</button>
      </div>
      <div v-else>
        <router-link type="button" class="btn btn-lg btn-primary btn-block" to="/all-products">繼續購物</router-link>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data(){
    return{
      order:{
        user:{},
      },
      orderId: '',
    }
  },
  methods: {
    getOrder(){
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`;
      vm.$store.state.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.order = response.data.order;
        console.log(response);
        vm.$store.state.isLoading = false;
      })
    },
    payOrder(){
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
      vm.$store.state.isLoading = true;
      this.$http.post(api).then((response) => {
        console.log(response);
        if(response.data.success){
          vm.getOrder()
        }
        vm.$store.state.isLoading = false;
      })
    }
  },
  created(){
    this.orderId = this.$route.params.orderId;
    this.getOrder();
  },
}
</script>